.filter-selector {

  &__title {
    background-color: #fcfcfc;
    height: 47px;
    padding: 0 20px;
    margin: 0;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;

    div {
      width: 60px;
      margin-right: auto;
      visibility: hidden;
    }

    button {
      margin-left: auto;
      width: 60px;
    }
    h4{
      display: flex;
      font-family: Helvetica, serif;
      text-transform: capitalize;
      font-size: 16px;
      font-weight: bold;
      font-style: normal;
      font-stretch: normal;
      line-height: normal;
      letter-spacing: normal;
      text-align: center;
      color: #333333;
    }
  }
}
